<template>
    <div class="product-view" v-if="productInfo">
        <page-title :title="productInfo.name[lang]"/>
        <div class="wrapper">
            <div class="product-section">
                <div class="flex-row-12">
                    <div class="flex-col xs-12 md-6">
                        <div class="images">
                            <div class="large">
                                <div class="slide"><img :src="productInfo.image"></div>
                            </div>
                        </div>
                    </div>
                    <div class="flex-col xs-12 md-6">
                        <div class="info">
                            <h3 class="info-title">{{ pageText.charecteristics[lang] }}</h3>
                            <div class="status">
                                <p class="available"></p>
                            </div>
                            <div class="characteristics">
                                <table>
                                    <tbody>
                                        <tr v-for="(option, i) in productInfo.characteristics" :key="i">
                                            <td>{{ option.name[lang] }}:</td>
                                            <td>{{ option.value[lang] }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                                <div class="price">
                                    <p class="current">{{ productInfo.cost }} UAH</p>
                                </div>
                                <div class="count">
                                    <p class="count-title">Кол-во</p>
                                    <div class="count-field">
                                        <button @click="count--">-</button>
                                        <input type="number" v-model="count" min="0" step="1">
                                        <button @click="count++">+</button>
                                    </div>
                                </div>
                                <div class="actions">
                                    <app-button v-if="!isProductInCart" :text="pageText.buyBtn[lang]" icon="fas fa-shopping-cart" @click="addToCart()" />
                                    <router-link v-if="isProductInCart" class="btn outline" :to="{ name: 'cart'}">
                                        <span class="btn-icon fas fa-check"></span>
                                        <span class="btn-text">{{ pageText.alreadyInCartBtn[lang] }}</span>
                                    </router-link> 

                                    <app-button v-if="!isProductInFavorites" class="outline" :text="pageText.addToFavoritesBtn[lang]" icon="fas fa-heart" @click="addToFavorites()"/>
                                    <router-link v-if="isProductInFavorites" class="btn outline" :to="{ name: 'favorites'}">
                                        <span class="btn-icon fas fa-heart"></span>
                                        <span class="btn-text">{{ pageText.alreadyInFavorites[lang] }}</span>
                                    </router-link> 
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="flex-col xs-12">
                        <div class="description">
                            <h4 class="d-title">{{ pageText.description[lang] }}</h4>
                            <p class="d-text" v-html="productInfo.description[lang]"></p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <questions-form />
    </div>
</template>

<script>
import AppButton from '../components/AppButton.vue';
import QuestionsForm from '../components/QuestionsForm.vue';
import PageTitle from '../components/PageTitle.vue';

export default {
    data() {
        return {
            count: 1,
            pageText: {
                charecteristics: {
                    ua: 'Характеристики',
                    ru: 'Характеристики',
                    en: 'Characteristics'
                },
                description: {
                    ua: 'Опис',
                    ru: 'Описание',
                    en: 'Description'
                },
                buyBtn: {
                    ua: 'Додати до кошика',
                    ru: 'Добавить в корзину',
                    en: 'Add to cart'
                },
                alreadyInCartBtn: {
                    ua: 'Вже у кошику',
                    ru: 'Уже в корзине',
                    en: 'Already in cart'
                },
                addToFavoritesBtn: {
                    ua: 'До обраного',
                    ru: 'В избранное',
                    en: 'Add to favorires'
                },
                alreadyInFavorites: {
                    ua: 'Вже в обраному',
                    ru: 'В избранном',
                    en: 'Already in favorites'
                },
                statusText: {
                    available: {
                        ua: 'В наявності',
                        ru: 'В наличии',
                        en: 'Available'
                    },
                    notAvailable: {
                        ua: 'Немає в наявності',
                        ru: 'Нет в наличии',
                        en: 'Not available'   
                    }
                }
            }
        }
    },  
    components: {
        QuestionsForm,
        PageTitle,
        AppButton
    },
    computed: {
        lang() {
            return this.$store.getters['getSelectedLang'];
        },
        categoryInfo() {
            return this.$store.getters['getCategoryById'](this.$route.params.categoryId);
        },
        productInfo() {
            return this.$store.getters['getProductById'](this.$route.params.productId);
        },
        isProductInCart() {
            return this.$store.getters['isProductInCart'](this.productInfo.code);
        },
        isProductInFavorites() {
            return this.$store.getters['isProductInFavorites'](this.productInfo.code);
        }
    },
    watch: {
        count(oldValue) {
            this.count = Math.round(this.count);
            if (oldValue <= 1) this.count = 1;
        }
    },
    methods: {
        openFilterWindow() {
            this.isFilterWindowOpen = true;
        },
        closeFilterWindow() {
            this.isFilterWindowOpen = false;
        },
        addToCart() {
            this.productInfo.count = this.count;
            this.$store.dispatch('addToCart', this.productInfo);
        },
        addToFavorites() {
            this.$store.dispatch('addToFavorites', this.productInfo.code);
        }
    }
}
</script>